.steps {
    padding-bottom: 130px;
    &__container {
        grid-template-columns: repeat(2, 1fr);
    }
    &__descr {
        max-width: 276px;
    }
    &__title {
        margin: 0;
                font-family: var(--un300);
font-size: 40px;
font-weight: 300;
line-height: 49.6px;
text-align: left;
        color: white;
        margin-bottom: 38px;
        
    }
    &__item {
        padding-top: 100px;
        padding-left: 55px;
        padding-bottom: 45px;
        &--first {
            background-image: url(../img/svg/step1-bg.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &--second {
            background-image: url(../img/svg/step2-bg.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &--third {
            background-image: url(../img/svg/step3-bg.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &--fourth {
            background-image: url(../img/svg/step4-bg.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}