.experience {
  &__top {
    height: 624px;
    background-image: url(../img/svg/experience-bg.png);

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-top: 84px;
    padding-bottom: 64px;
    &--container {
        display: flex;
        flex-direction: column;
        height: 100%;
    }
  }
  &__subtitles {
    display: flex;
    justify-content: space-between;
  }
  &__subtitle {
    font-family: var(--in400);
    font-size: 12px;
    font-weight: 400;
    line-height: 14.52px;
    text-align: left;
    color: white;
    flex-grow: 1;
    &:first-child {
      flex-grow: 4;
      @include gradient-text;
    }
    &--container {
        display: flex;
        flex-direction: column;
    }
  }
  &__text {
    display: flex;
    margin-top: auto;
  }
  &__descr {
    // @include gradient-text;
    max-width: 432px;
    &--white {
        color: white;
    }
    &--gradient {
        @include gradient-text;
        max-width: 350px;
    }
  }
  &__title {
    max-width: 700px;
      span {
      position: relative;
    }
    span::after {
      position: absolute;
      content: "˜˜˜˜˜˜˜˜";
      top: 50%;
      left: 50%;
      transform: translate(-54%, -84%);
      font-family: var(--un300);
      font-size: 48px;
      font-weight: 300;
      line-height: 59.52px;
      letter-spacing: -0.2em;
      text-align: left;
    }
  }
  &__bottom {
    padding: 97px 0 130px 0;
    .container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
  }
  &__descrs {
    display: flex;
    gap: 50px;
  }
  
}
