.about {
    padding-top: 104px;
    padding-bottom: 150px;
    &__text {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        max-width: 764px;
    }
    &__title {
        
        margin-top: 40px;
        margin-bottom: 64px;
        max-width: 709px;
        span {
            position: relative;
        }
        span::after {
      position: absolute;
      content: '˜˜˜˜˜˜˜˜˜˜˜˜˜˜˜˜˜';
      top: 50%;
      left: 50%;
      transform: translate(-50%, 50%);
        font-family: var(--un300);
font-size: 48px;
font-weight: 300;
line-height: 59.52px;
letter-spacing: -0.2em;
text-align: left;
      
    }
    }
    &__descr {
        width: 432px;
    }
    &__subtitle {
        font-family: var(--in400);
font-size: 12px;
font-weight: 400;
line-height: 14.52px;
text-align: left;
        @include gradient-text();
    }   
    &__container {
        align-items: flex-end;
        justify-content: space-between;
    }
    &__group {
        padding-bottom: 33px;
        
        background: #202020;
    }
    &__year {
        
            font-family: var(--in400);
font-size: 14px;
font-weight: 400;
line-height: 16.94px;
text-align: center;
        @include gradient-text();
    }
}