.header {
  position: absolute;
  width: 100%;
  top: 48px;
  
}

.site-nav {
  border: 1px solid white;
  display: flex;
  height: 134px;
  align-items: center;
  &__logo {
    height: 100%;
    padding-left: 31px;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    &--image {
      margin-right: 57px;
    }
    &--text {
      font-family: var(--un600);
      font-size: 10px;
      font-weight: 600;
      line-height: 12.4px;
      text-align: left;
      color: white;
    }
  }
  &__menu {
    width: 100%;
    height: 100%;
  }
  &__list {
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  &__item {
    flex-grow: 1;
    height: 100%;
    &--world {
      max-width: 66px;
    }
  }
  &__link {
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-transform: uppercase;
    font-family: var(--in500);
    font-size: 12px;
    font-weight: 500;
    line-height: 14.52px;
    color: white;
  }
  &__list--mob {
    border: 1px solid black;
    display: none;
    &--active {
      display: block;
    }
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 6%);
    width: 350px;
    .site-nav__item--world {
      max-width: none;
    }
    .site-nav__item {
      height: 60px;
      background: white;
      border: 1px solid black;
    }
    .site-nav__link {
      color: black;
    }
  }
}



.burger {
  border: 1px solid white;
  height: 100%;
  width: 100%;
  text-transform: uppercase;
  color: white;
    font-family: var(--in500);
font-size: 12px;
font-weight: 500;
line-height: 14.52px;
text-align: center;
display: none;

}