// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1250px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --purple-color: #F983E9;

  // fonts
  --font-family: Unbounded;
  --second-family: Inter Tight;

--in400: InterTight-Regular;
--in500: InterTight-Medium;
--un250: Unbounded-ExtraLight;
--un300: Unbounded-Light;
--un400: Unbounded-Regular;
--un600: Unbounded-SemiBold;



}
