@media screen and (max-width: 1200px) {
  .container {
    width: 1440px;
  }
  .site-nav__logo--text {
    display: none;
  }
  .header {
  }
  .hero {
    &__list {
      grid-template-columns: repeat(2, 240px);
    }
  }
  .about {
  }
  .steps {
  }
  .info {
  }
  .experience {
  }
  .footer {
  }
}

@media screen and (max-width: 1199px) {
  .container {
    width: 960px;
  }
  .header {
  }
  .section {
    &__title {
      font-size: 32px;
      font-weight: 300;
      line-height: 39.68px;

      span::after {
        font-size: 32px;
      }
    }
    
  }
  .hero {
    &__title--second {
      font-size: 12px;
      max-width: 180px;
    }
  }
  .about {
    &__text {
        display: block;
    }
    &__title {
        span::after {
            transform: translate(-50%, 20%);
        }
    }
  }
  .steps {
  }
  .info {
  }
  .experience {
    &__bottom {
        .container {
            display: block;
        }
    }
  }
  .footer {
  }
}

@media screen and (max-width: 991px) {
  .container {
    width: 720px;
  }
  .site-nav__menu {
    display: none;
    
  }
  .site-nav {
    height: 60px;
  }
  .burger {
    display: block;
  }
  .header {
  }
  .hero {
  }
  .about {
  }
  .steps {
    &__container {
        display: block;
    }
  }
  .info {
    &__wrapper {
        display: block;
    }
    &__star {
        display: none;
    }
    &__descr--container {
        display: block;
    }
  }
  .service__group {
    display: block;
    max-width: 306px;
  }
  
  .experience {
  }
  .footer {
  }
  .site-nav--foter {
    display: block;
  }
  .section {
    &__descr {
        max-width: 350px;
    }
  }
}

@media screen and (max-width: 767px) {
  .container {
    width: 540px;
  }
  .header {
  }
  .hero {
  }
  .about {
    &__group {
        display: none;
    }
  }
  .steps {
  }
  .info {
    &__title {
        span::after {
            content: '';
        }
    }
  }
  .experience {
    &__descrs {
        display: block;
    }
    &__text {
        display: block;
    }
  }
  .footer {
  }
}

@media screen and (max-width: 575px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
  
  .header {
  }
  .hero {
    padding-top: 200px;
    &__left {
      display: flex;
      flex-direction: column;
    }
    &__text {
      display: block;
    }

    &__subtitle {
      margin-left: auto;
    }
    &__list {
        grid-template-columns: repeat(2, 130px);
    }
    &__item {
        max-width: 130px;
    }
    &__link {
        font-size: 15px;
    }
  }
  .about {
    &__title {
        span::after {
            transform: translate(-50%, 5%);
        }
    }
  }
  .steps {
  }
  .info {
  }
  .experience {
  }
  .footer {
  }
}
