.hero {
  display: flex;
  flex-direction: column;
  padding-top: 400px;
  min-height: 912px;
  background: url(../img/svg/hero-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  &__container {
    width: 100%;
  }
  &__subtitle {
    margin: 0;
    display: flex;
    gap: 25px;
    font-family: var(--un250);
    font-size: 27px;
    font-weight: 250;
    line-height: 33.48px;
    text-align: left;
    @include gradient-text();
    margin-bottom: 67px;
    span {
      font-family: var(--in400);
      font-size: 12px;
      font-weight: 400;
      line-height: 14.52px;
      text-align: left;
    }
  }
  &__title {
    // @include gradient-text();
    &--second {
      margin: 0;

      display: block;
      font-family: var(--un400);
      font-size: 16px;
      font-weight: 400;
      line-height: 19.84px;
      text-align: left;
      max-width: 300px;
      color: white;
    }
    span {
      position: relative;
    }
    span::after {
      position: absolute;
      content: "˜˜˜˜˜˜˜˜˜˜";
      top: 50%;
      left: 50%;
      transform: translate(-54%, -84%);
      font-family: var(--un300);
      font-size: 48px;
      font-weight: 300;
      line-height: 59.52px;
      letter-spacing: -0.2em;
      text-align: left;
    }
  }
  &__text {
    display: flex;
    align-items: end;
    justify-content: space-between;
    margin-bottom: 30px;
  }
  &__descr {
    margin: 0;
    font-family: var(--in400);
    font-size: 14px;
    font-weight: 400;
    line-height: 16.94px;
    text-align: left;
    @include gradient-text();
    max-width: 216px;
    margin-top: 21px;
  }
  &__bottom {
    background: #000000b2;
    padding: 40px 0 50px 0;
    margin-top: auto;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 240px);
    justify-content: space-between;
  }

  &__item {
    width: 240px;

    border-bottom: 2px solid white;
    
  }

  &__link {
    display: block;
    
    font-family: var(--un400);
    font-size: 15px;
    font-weight: 400;
    line-height: 18.6px;
    text-align: left;
    @include gradient-text;
    padding: 23px 0;
  }
}
