.info {
  padding-bottom: 122px;
  &__title {
    max-width: 780px;
    margin-bottom: 83px;
    span {
      position: relative;
    }
    span::after {
      position: absolute;
      content: "˜˜˜˜˜˜˜˜˜˜˜˜˜˜";
      top: 50%;
      left: 50%;
      transform: translate(-54%, -84%);
      font-family: var(--un300);
      font-size: 48px;
      font-weight: 300;
      line-height: 59.52px;
      letter-spacing: -0.2em;
      text-align: left;
    }
  }
  &__content {
    width: 918px;
  }
  &__descr--container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }
  &__descr {
    width: 432px;
  }
  &__subtitle {
    font-family: var(--in400);
    font-size: 12px;
    font-weight: 400;
    line-height: 14.52px;
    text-align: left;
    @include gradient-text;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
  }
  &__star {
    margin-top: 500px;
  }
}

.service {
  &__group {
    display: flex;
    margin-bottom: 32px;
  }
  &__item {
    flex-grow: 1;
  }
  &__group,
  &__head,
  &__descr {
    border: 1px solid white;
  }

  &__head {
    height: 145px;
    display: flex;
    align-items: center;
    padding: 22px;
  }
  &__descr {
    padding-left: 36px;
    padding-top: 36px;
    padding-bottom: 47px;
    p {
      max-width: 182px;
    }
  }
  &__title {
    font-size: 30px;
    font-weight: 300;
    line-height: 37.2px;
  }
}
